
import React from 'react';
import { TextareaItem } from 'antd-mobile';

class SearchArea extends React.Component {

  constructor(props, context) {
    super(props, context);
    this.onSearchDonate = this.onSearchDonate.bind(this);
  }

  onSearchDonate() {
    console.log(this.refs.search.state.value);
  }

  render() {
    return (
      <div style={styles.searchArea}>
        <span style={{...styles.searchTitle, ...styles.textOverflow}}>捐款查询平台</span>
        <div style={styles.inputBox}>
          <img
            src="data:image/png;base64,"
            alt=""
            style={styles.searchButton}
            onClick={this.onSearchDonate}
          />
          <TextareaItem
            placeholder="单位查询直接输入名称，个人查询请输入姓名+手机号后四位，如韩星0000"
            data-seed="logId"
            autoHeight
            ref="search"
            style={{paddingRight: '1rem'}}
          />
        </div>
      </div>
    );
  }

}

export default SearchArea;

const styles = {
  textOverflow: {
    textOverflow: 'ellipsis',
    overflow: 'hidden',
    whiteSpace: 'nowrap'
  },
  searchArea: {
    display: 'flex',
    marginTop: '2.5rem',
    height: '18rem',
    padding: '4rem',
    backgroundColor: '#ffffff'
  },
  searchTitle: {
    width: '32rem',
    height: '10rem',
    paddingTop: '1rem',
    lineHeight: '8rem',
    fontSize: '5.2rem',
    color: '#333333'
  },
  inputBox: {
    flex: 1,
    height: '10rem',
    marginLeft: '4rem',
    border: '1px solid #999999',
    borderRadius: '5rem',
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center'
  },
  searchButton: {
    width: '5rem',
    height: '5rem',
    marginLeft: '4rem'
  }
};


